* {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  padding: 60px;
}

input{
  color: gray;
  width: 99.5%;
  font-size: 1.2rem;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  overflow: hidden;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
}